<template>
    <div class="g-content">
        <!--  -->
        <div class="m-query-form fr">
            <Form ref="queryForm" :model="queryForm" :rules="validate">
                <Form-item class="query-item">
                    <Input v-model="queryForm.id" placeholder=""></Input>
                </Form-item>
                <Form-item class="fl" prop="date">
                    <Date-picker class="query-item" type="date" v-model="queryForm.sTime" placement="bottom-end" placeholder="-" @on-change="getStartDate"></Date-picker>
                    <Date-picker class="query-item" type="date" v-model="queryForm.eTime" placement="bottom-end" placeholder="-" @on-change="getEndDate"></Date-picker>
                    <div class="clearfix"></div>
                </Form-item>
                <Form-item class="fl">
                    <Button class="query-button" type="primary" @click="query('queryForm', 'valid')"></Button>
                    <Button class="query-button" type="ghost" @click="resetQuery('queryForm')"></Button>
                </Form-item>
            </Form>
        </div>
        <!--  -->
        <div class="m-operation fl">
            <router-link :to="{ name: 'StoreArticle' }" class="operation-btn ivu-btn ivu-btn-primary"></router-link>
            <Button class="operation-btn" :disabled="selectList.length == 0" type="warning" @click="deleteData"></Button>
        </div>
        <div class="clearfix"></div>
        <!--   -->
        <Loading v-if="pageLoading"></Loading>
        <div v-else>
            <!--  -->
            <Table
                class="m-table-list"
                border
                :columns="userList"
                :data="listData"
                @on-selection-change="setSelectList"
            ></Table>
        </div>
        <!--  -->
        <Page
            class-name="m-page"
            show-elevator
            show-sizer
            show-total
            :total="page.dataCount"
            :page-size="page.pageSize"
            :current="page.pageNo"
            :page-size-opts="page.pageSizeOpts"
            @on-change="changePage"
            @on-page-size-change="changePageSize"
        >
        </Page>
        <div class="clearfix"></div>
    </div>
</template>

<script>
    // 
    import Loading from '@/components/Common/Loading'
    // 
    import Validate from 'common/validate.js'
    // Api
    import Api from '@/api/article_list.js'
    // 
    import tableSetting from 'common/table_setting.js'
    // 
    import TableQuery from 'mixins/table_query.js'
    // 
    import TableOperate from 'mixins/table_operate.js'
    // 
    import Page from 'mixins/page.js'

    export default {
        components: { Loading },
        mixins: [ TableQuery, TableOperate, Page ],
        computed: {
            // 
            apiGetAll(){
                return () => Api.GetArtcList(this.page.pageNo, this.page.pageSize);
            },
            // 
            apiGetFilter(){
                return () => Api.FilterArtcList(this.queryForm, this.page.pageNo, this.page.pageSize);
            },
            // 
            apiDelete(){
                return () => Api.DeleteArtc(this.selectList);
            },
        },
        data() {
            return {
                // 
                pageLoading: true,
                // 
                showModal: false,
                // id
                editId: '',
                // :1, 2
                operateType: 0,
                // 
                queryForm: {
                    // 
                    id: '',
                    // 
                    sTime: '',
                    // 
                    eTime: '',
                },
                // 
                validate: {
                    date:[{
                        validator: (rule, value, callback) => {
                            Validate.ValidRangeDate(this.queryForm.sTime, this.queryForm.eTime, callback, false);
                        },
                        trigger: 'change',
                    }]
                },
                // 
                userList:[
                    {
                        title: '',
                        type: 'selection',
                        key: 'selectAll',
                        width: '60',
                        align: 'center',
                    },
                    {
                        title: '',
                        key: 'id',
                        align: 'center',
                    },
                    {
                        title: '',
                        key: 'title',
                        align: 'center'
                    },
                    {
                        title: '',
                        key: 'tags',
                        align: 'center',
                        render: (h, params) => {
                            return h('span', {}, params.row.tags.toString());
                        }
                    },
                    {
                        title: '',
                        key: 'CreatedTime',
                        align: 'center'
                    },
                    {
                        title: '',
                        key: 'UpdateTime',
                        align: 'center'
                    },
                    {
                        title: '',
                        key: 'operation',
                        width: '120',
                        align: 'center',
                        render: (h, params) => {
                            return h('div', [
                                tableSetting.gotoDetail(h, params, this, '', 'StoreArticle'),
                            ]);                          
                        }
                    }
                ],
                //
                listData:[],
            }
        },
        created(){
            // 
            this.$store.commit('SET_BREADCRUMB', [
                { name: '', path: '/Home' },
                { name: '', path: '' }
            ]);
            // 
            this.getTableList();
        },
        methods: {
            // 
            getTableList(query){
                this.pageLoading = true;
                // 
                if(query){
                    this.isQuery = true;
                    this.getFilterList();
                }
                else{
                    this.isQuery = false;
                    this.getAllList();
                }
            },
            // 
            setListData(result){
                if(result.length > 0){
                    this.listData = result.map(item => {
                        return {
                            id: item.id,
                            title: item.attributes.title,
                            dataFrom: item.attributes.dataFrom,
                            tags: item.attributes.tags,
                            CreatedTime: item.createdAt,
                            UpdateTime: item.updatedAt
                        }
                    });
                }
                else this.listData = [];
            }
        }
    }
</script>

<style lang="less" scoped>
    @import "../../../assets/less/table_list";
</style>


