<template>
    <div id="imgCropper" class="m-Upload">
        <!--  -->
        <div v-if="getImageUrl != ''" class="upload-show">
            <div class="upload-img-wrap"><img class="upload-image" :src="getImageUrl" alt="" @error="notFoundPic"/></div>
            <!--  -->
            <div v-if="preview" class="upload-mask">
                <Icon type="eye" @click.native="viewImage" style="margin-right:15px;"></Icon>
                <Icon type="edit" @click.native="uploadClick"></Icon>
            </div>            
        </div>
        <div class="clearfix"></div>
        <!--  -->
        <div v-show="showUploadBtn" class="upload-btn" @click="uploadClick">
            <Icon type="camera" size="20"></Icon>
            <input ref="imgFile" type="file" :accept="format" hidden @change="selectFile"/>
        </div>
        <div class="clearfix"></div>
        <!--   -->
        <template v-if="uploadStatus != 'finished'">
            <Progress v-if="showProgress" :percent="percentage" hide-info :stroke-width="3" style="width:340px;"></Progress>
        </template>
        <br/>
        <!--  -->
        <span class="hint">
            * jpgjpegpng
            <span v-if="sizeHint"> {{ sizeHint }}</span>
            
            <span v-if="fileSize < 1024">{{ fileSize }}kb</span>
            <span v-else>{{ Math.floor(fileSize/1024) }}M</span>
                        
        </span>
        <!--  -->
        <Modal :title="cropImgModel.title" class="m-cropper-img" v-model="cropImgModel.show" width="900" @on-cancel="hideCropModel">
            <!--  -->
            <div class="img-cropper-area">
                <vueCropper
                    ref="cropper"
                    :img="option.img"
                    :outputSize="option.size"
                    :outputType="option.outputType"
                    :info="true"
                    :canScale="option.canScale"
                    :autoCrop="option.autoCrop"
                    :autoCropWidth="autoCropWidth"
                    :autoCropHeight="autoCropHeight"
                    :fixed="fixed"
                    :fixedNumber="fixedNumber"
                    @realTime="realTime"
                ></vueCropper>
            </div>
            <!--  -->
            <div class="img-preview-area">
                <div class="preview">
                    <img v-if="cropImgUrl" :src="cropImgUrl" alt="" @error="notFoundPic">
                    <span v-else></span>
                </div>
            </div>
            <div class="clearfix"></div>
            <!--  -->
            <div class="img-button-area">
                <Button type="primary" @click="changeScale(1)"><Icon type="plus"></Icon></Button>
                <Button type="primary" @click="changeScale(-1)"><Icon type="minus"></Icon></Button>
                <Button type="primary" @click="rotateRight"><Icon type="refresh"></Icon></Button>
                <Button type="primary" @click="previewImg('blob')"></Button>
            </div>
            <!--  -->
            <div style="margin-top:10px">
                <span class="hint">
                    * jpgjpegpng
                    <span v-if="sizeHint"> {{ sizeHint }}</span>
                    
                    <span v-if="fileSize < 1024">{{ fileSize }}kb</span>
                    <span v-else>{{ Math.floor(fileSize/1024) }}M</span>
                                
                </span>
            </div>
            <!--  -->
            <div slot="footer">
                <Button type="ghost" @click="hideCropModel"></Button>
                <Button type="primary" @click="comfirCropper"></Button>
            </div>
        </Modal>
        <!--  -->
        <Modal title="" class="m-view-img" v-model="showModal" width="600">
            <img :src="getImageUrl" @error="notFoundPic"/>
            <div slot="footer"></div>
        </Modal>
    </div>
</template>

<script>
    // 
    import vueCropper from 'vue-cropper'
    // JS
    import Common from 'common/common.js'
    // 
    import UploadImg from 'mixins/upload_img.js'
    // axios
    import axios from 'axios'

    export default {
        name: 'imgCropper',
        mixins: [ UploadImg ],
        components: { vueCropper },
        // 
        props: {
            // Url
            src:{
                type: String,
                default: ''
            },
            // 
            preview:{
                type: Boolean,
                default: false
            },
            // 
            sizeHint:{
                type: String,
                default: '100*100px'
            },
            // KB
            fileSize:{
              type: Number,
              default: 150
            },
            // 
            autoCropWidth:{
                type: Number,
                default: 100
            },
            // 
            autoCropHeight:{
                type: Number,
                default: 100
            },
            // 
            fixed:{
                type: Boolean,
                default: false
            },
            // 
            fixedNumber: {
                type: Array,
                default: () => [4, 3]
            },
        },
        data () {
            return {
                // 
                showUploadBtn: true,
                // 
                cropImgModel: {
                    title: '',
                    show: false
                },
                // https://github.com/xyxiao001/vue-cropper
                option: {
                    img: '',
                    size: 1,
                    outputType: 'jpg',
                    canScale: false,
                    autoCrop: true
                },
                // 
                previews: {},
                // url
                cropImgUrl: '',
                // 
                getImageUrl: ''
            }
        },
        watch: {
			src(val) {
                if(val){
                    this.showUploadBtn = false;
                    this.getImageUrl = this.src;
                }
			}
        },
        created() {
            if(this.src){
                this.showUploadBtn = false;
                this.getImageUrl = this.src;
            }
        },
        methods: {
            // 
            uploadClick(){
                // 
                this.$refs.imgFile.click();
            },
            // 
            comfirCropper(){                
                this.progressShow();

                // 
                let progress = setInterval(() => {
                    if(this.percentage == 90) clearInterval(progress);
                    this.percentage += 10;
                },100);

                const fileList = this.$refs.imgFile.files;

                // 
                this.$refs.cropper.getCropBlob((data) => {

                    data.lastModifiedDate = new Date();
                    data.name = fileList[0].name;

					// formData
                    let params = new FormData();
                    // tokentokentoken
                    params.append('token', Common.UPLOAD_TOKEN);
                    params.append('file', data);
                    
                    // incorrect region, please use up-z2.qiniu.com
                    axios.post('http://upload.qiniu.com/', params)
                    .then(res => {
                        let url = Common.UPLOAD_URL + res.data.hash;
                        this.getImageUrl = url;
                        // url
                        this.$emit('get-img-url', url);
                        
                        // 
                        this.progressHide();
                        this.percentage = 100;
                        clearInterval(progress);                        

                        this.showUploadBtn = false;
                        this.hideCropModel();

                        this.$Notice.success({ title: '!' });
                    })
                    .catch(err => {
                        // 
                        this.progressHide();                 
                        clearInterval(progress);
                        this.$Notice.error({ title: '' });
                        this.hideCropModel();
                    })
				})
            },
            // 
            hideCropModel(){
                this.cropImgModel.show = false;
                this.$refs.cropper.clearCrop(); 
                this.option.img = '';
                this.cropImgUrl = '';
            },
            // 
            selectFile(){
                // 
                const fileList = this.$refs.imgFile.files;

                if(fileList.length > 0){
                    let fileName = fileList[0].name;
                    // kb
                    let fileSize = Math.floor(fileList[0].size / 1024);

                    let maxSize = this.fileSize;                
                    let overHint = maxSize + 'kb';
                    // 
                    if(fileSize > maxSize){
                        if(maxSize > 1024) overHint = Math.floor(maxSize/1024) + 'M';
                        this.$Notice.warning({
                            title: '',
                            desc: ' ' + fileName + '  ' + overHint
                        });
                        this.progressHide();
                        return false;
                    }

                    // 
                    this.cropImgModel.show = true;                    

                    let file = fileList[0];
                    // FileReader API base64  
                    let reader = new FileReader();
                    reader.onload = (e) => {
                        let data;
                        if (typeof e.target.result === 'object') {
                            // Array Bufferblob base64
                            data = window.URL.createObjectURL(new Blob([e.target.result]));
                        }
                        else {
                            data = e.target.result;
                        }

                        this.option.img = data;

                        // 
                        $('.img-cropper-area').height('500px');
                    }
                    // base64
                    // reader.readAsDataURL(file);
                    // blob
                    reader.readAsArrayBuffer(file);
                }
                else console.log('');
            },
            // 
            changeScale(num){
                this.$refs.cropper.changeScale(num);
            },
            // 
            rotateRight(){
                this.$refs.cropper.rotateRight();
            },
            // 
            realTime (data) {
                this.previews = data;
            },
            // 
            previewImg (type) {
                // 
                if (type === 'blob') {                    
                    this.$refs.cropper.getCropBlob((data) => {
                        this.cropImgUrl = window.URL.createObjectURL(data);
                    })
                }
                else {
                    this.$refs.cropper.getCropData((data) => {
                        this.cropImgUrl = data;
                    })
                }

                // 
                // let test = window.open('about:blank');
			    // test.document.body.innerHTML = '..';
                // if (type === 'blob') {                    
                //     this.$refs.cropper.getCropBlob((data) => {
                //         test = window.open('');
                //         test.location.href = window.URL.createObjectURL(data);
                //     })
                // }
                // else {
                //     this.$refs.cropper.getCropData((data) => {
                //         test.location.href = data;
                //     })
                // }
            },
        }
    }
</script>

<style lang="less" scoped>
    @import "../../assets/less/upload_img";

    .upload-btn{
        width: 90px;
        height: 90px;
        line-height: 90px;
        background: #fff;
        border: 1px dashed #d7dde4;
        border-radius: 4px;
        text-align: center;
        cursor: pointer;
        position: relative;
        overflow: hidden;
        -webkit-transition: border-color .2s ease;
        transition: border-color .2s ease;

        &:hover{
            border: 1px dashed #00c5a3;
        }
    }

    .img-cropper-area{
        width: 500px;
        float: left;
        margin-right: 35px;
    }

    .img-preview-area{
        width: 330px;
        min-height: 100px;
        line-height: 100px;
        text-align: center;
        overflow: hidden;
        float: left;

        img{
            width: 100%;
            display: block;
        }

        .preview{
            width: 100%;
            border: 1px dashed #e9eaec;
        }
    }

    .img-button-area{
        margin-top: 10px;

        button{
            margin-right: 10px;
        }
    }

    .upload-show{
        margin-bottom: 0;
    }
</style>


