<template>
    <div class="g-content">
        <!--   -->
        <Loading v-if="pageLoading"></Loading>
        <div v-else>
            <Form ref="infoForm" :model="infoForm" :rules="validate" :label-width="100">
                <Form-item label="" prop="title">
                    <Input v-model="infoForm.title" placeholder=""></Input>
                </Form-item>
                <Form-item label="" prop="tags">
                    <Select v-model="infoForm.tags" multiple placeholder="()">
                        <Option v-for="item in tagList" :value="item.name" :key="item.id">{{ item.name }}</Option>
                    </Select>
                </Form-item>
                <Form-item label="" prop="content">
                    <quill-editor
                        class="instruction-editor"
                        v-model="infoForm.content"
                        ref="myQuillEditor"
                        :options="editorOption"
                        @ready="onEditorReady($event)"
                    >
                    </quill-editor>
                </Form-item>
                <!--  -->
                <div class="m-operation">
                    <Button v-if="pageType == 'edit'" class="fr" type="primary" @click="submit('infoForm')"></Button>
                    <Button v-else class="fr" type="primary" @click="submit('infoForm')"></Button>
                    <Button class="fr" type="ghost" @click="$router.go(-1)"></Button>
                    <div class="clearfix"></div>
                </div>
            </Form>
        </div>
    </div>
</template>

<script>
    // 
    import Loading from 'components/Common/Loading'
    // JS
    import { GetUrlQuery } from 'common/important.js'
    // Api
    import Api from 'api/article_list.js'
    // Json
    import JsonData from 'mock/data.json'
    // Vue-quill-editor
    import Quill from 'quill'
    // QullEditor
    import { ImageImport } from 'qullEditor/ImageImport.js'
    import { ImageResize } from 'qullEditor/ImageResize.js'
    Quill.register('modules/imageImport', ImageImport)
    Quill.register('modules/imageResize', ImageResize)
    
    export default {
        components: { Loading },
        data() {
            return {
                // 
                pageLoading: true,
                // 
                pageType: 'add',
                // 
                tagList: [],
                // 
                editorOption:{
                    placeholder: '',
                    modules:{
                        imageImport: true,
                        imageResize: {
                            displaySize: true
                        }
                    }
                },
                // id
                articleId: '',
                // 
                infoForm: {
                    title: '',
                    tags: [],
                    content: '',
                },
                // 
                validate: {
                    title: [{ required: true, message: '', trigger: 'blur'}],
                    content: [{ required: true, message: '', trigger: 'blur'}]
                },
            }
        },
        created() {
            let id = GetUrlQuery('id');
            // 
            this.tagList = JsonData.articleTags;
            // 
            if(id){
                // 
                this.articleId = id;
                this.$store.commit('SET_BREADCRUMB', [
                    { name: '', path: '/Home' },
                    { name: '', path: '/Examples/ArticleList' },
                    { name: '', path: '' }
                ]);
                // 
                this.getDetail();
                this.pageType = 'edit';
            }
            else{
                this.pageLoading = false;
                this.$store.commit('SET_BREADCRUMB', [
                    { name: '', path: '/Home' },
                    { name: '', path: '/Examples/ArticleList' },
                    { name: '', path: '' }
                ]);
            }
        },
        methods: {
            // 
            submit(form) {
                this.$refs[form].validate((valid) => {
                    if (valid) {
                        // 
                        this.pageLoading = true;
                        
                        if(this.pageType == 'add'){
                            // 
                            Api.AddArticle(this.infoForm)
                            .then(res => {
                                // 
                                this.pageLoading = false;
                                if(res.code == 200){
                                    this.$Message.success({
                                        content: '!',
                                        onClose: () => {
                                            // 
                                            this.$router.push({ name: 'ArticleList' });
                                        }
                                    }); 
                                }
                                else console.log(res);
                            })
                            .catch(err => this.$Message.warning(err.message))
                        }
                        else{
                            console.log(this.infoForm);
                            // 
                            Api.EditArticle(this.infoForm, this.userId)
                            .then(res => {
                                // 
                                this.pageLoading = false;
                                if(res.code == 200){
                                    this.$Message.success('!');
                                    this.getDetail();
                                }
                                else this.$Message.warning(res.msg);
                            })
                            .catch(err => console.log(err))
                        }
                    }
                    else this.$Message.error('');
                })    
            },
            // 
            getDetail(){
                this.pageLoading = false;
                // Api.GetArtcDetail(this.articleId)
                // .then(res => {                    
                //     // 
                //     this.pageLoading = false;
                //     const result = res.data.attributes;                    
                //     if(res.code == 200){
                //         // 
                //         this.infoForm = result;
                //     }
                //     else this.$Message.warning(res.msg);
                // })
                // .catch(err => console.log(err))
            },
            // 
            onEditorReady(editor) {
            },
            // 
            onEditorChange({ editor, html, text }) {
                this.content = html
            }
        }
    }
</script>

<style lang="less">
    @import "../../../assets/less/color";
    .m-operation {
        button, a {
            min-width: 80px;
            margin-left: 10px;
        }
    }
    
    .instruction-editor{
        background: #fff;
        .ql-toolbar{
            border-radius: 4px 4px 0 0;
            border: 1px solid @table_border_color;
        }
        .ql-container{
            min-height: 500px;
            border-radius: 0 0 4px 4px;
            border: 1px solid #eee;
            .ql-editor{
                padding-bottom: 20px;
            }
        }
    }
</style>


